@import url('https://fonts.googleapis.com/css2?family=Caveat+Brush&family=Montserrat:wght@500&display=swap');
.A1wrapper {
    margin : 0;
    padding : 0;
    width: 100vw;
    height: 120vh;
    animation: appear 2s;

    .wrapper {
        position: relative;
        top : 18vh;
    }
    .contentDiv {
        display: flex;
        margin-bottom: 15vh;

        p {
            max-width: 50vw;
            color : white;
            font-family: 'Caveat Brush' ,cursive;
            font-size: x-large;
            padding-left: 5vw;
            padding-top: 3vh;
        }
        @media screen and (orientation : portrait) {
            img {
                width : 10vw;
                height : 10vw;
            }
        }
        img {
            padding-left: 5vw;
            padding-top: 3vh;
            width : 35vw;
            height : 40vh;
        }
    }
    h1 {
        color : white;
        display: flex;
        justify-content: center;
        transform: scale(2);
        position: relative;
        top : 18vh;
        font-family: 'Caveat Brush' ,cursive;
    }

}
@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
body {
    overflow-x: hidden;
    background: url('../../assets/4k1.jpg');
    background-size: cover;
    background-attachment: fixed;
}
