@import url('https://fonts.googleapis.com/css2?family=Caveat+Brush&family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');
.ul {
    font-family: 'Caveat Brush' ,cursive;
    display: flex;
    list-style: none;
    position: fixed;
    z-index: 999;
    width : 100vw;
    background-image: url('https://mdbootstrap.com/img/Photos/Others/background.jpg');

    .listItem {
        display: flex;
        justify-content: center;
        margin-left: 5vw;
        margin-right: 5vw;
        width: 20vw;
        font-size: x-large;
        margin-top : 5vh;
        font-family: 'Montserrat' sans-serif;

       

        .link {
            text-decoration: none;
            color : #818181;
            font-size: x-large;
            font-family: 'Montserrat' sans-serif;
        }
    }
    .choosed {
        color : rgb(144, 83, 201);
        font-weight: 700;
        font-family: 'Montserrat' sans-serif;
    }
}

* {
    margin: 0;

}
.h1 {
    font-size: xx-large;
    color : rgb(144, 83, 201);
    text-align: center;
}

